import Vue from "vue";
import Vuex from "vuex";
import http from "@/utils/http-require.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    mainPage: "home",
    subPage: "Header",
    param: "",

    isAuth: false,

    members: {
      membersId: "",
      name: "",
      email: "",
      address: "",
      phone: "",
      account: "",
      image: "",
      portfolio: "",
      rank: 0
    }
  },
  mutations: {
    selectMemeber(state, payload) {
      http.get(`member/search/${payload}`).then(res => {
        state.members = res.data;
      });
    },

    /*
      this.$store.commit( 'pageOf' , { mainPage : '나의 메인 페이지 home,about,command' ,(필수)subPage : "content에 렌더링할 페이지", param : "조회에 필요할 id"})

      ex) this.$store.commit('pageof',{mainPage : "command" , subPage : "detailpage", param : pageId})
    */
    pageOf(state, payload) {
      state.subPage = payload.subPage;
      if (typeof payload.mainPage == "undefined") {
        state.subPage = payload.subPage;
      } else state.mainPage = payload.mainPage;
      state.param = payload.param;
      console.log(`page of : ${state.mainPage} , ${state.subPage}, ${state.param}`);
    }
  },
  actions: {},
  modules: {},
  getters: {}
});
