<template>
  <div id="AuctionCancel">
    <h2 style="line-height:2em">경매 취소</h2>
    <table>
      <thead>
        <td>마감일</td>
        <td>경매명</td>
        <td>내용</td>
        <td>마감시간</td>
        <td>경매파기</td>
      </thead>
      <tbody id="auction-list" :key="index" v-for="(auction, index) in auctions">
        <td>{{auction.deadline}}</td>
        <td>{{auction.title}}</td>
        <td>{{auction.contents}}</td>
        <td>{{auction.sales_time}}</td>
        <td>
          <button @click="deletedata(auction,index)" style="width:100%">삭제</button>
        </td>
      </tbody>
    </table>
    <carousel :per-page="1" :mouse-drag="true" :autoplay="true" :loop="true">
      <slide :key="index" v-for="(auction, index) in auctions">
        <div class="w3-row-padding" style="padding-top:30px">
          <div class="w3-third" style="margin-left:33%">
            <ul class="w3-ul w3-border w3-white w3-center w3-opacity w3-hover-opacity-off">
              <img :src="getThumb(auction)" style="width:100%" class="w3-hover-opacity" />
              <li class="w3-padding-16">{{ auction.title }}</li>
              <li class="w3-padding-16">{{auction.contents}}</li>
              <li class="w3-padding-16">
                <i class="fa fa-star font-size-h6 color-dark-yellow-400" />
                마감일 : {{auction.deadline}}
                <br />
                <i class="fa fa-star font-size-h6 color-dark-yellow-400" />
                경매끝 : {{auction.salesTime}}
              </li>
              <li class="w3-padding-16">
                <h2>₩ {{auction.maxPrice}}</h2>
              </li>
              <li class="w3-light-grey w3-padding-24">
                <button class="w3-button w3-teal w3-padding-large w3-hover-black">조회하기</button>
              </li>
            </ul>
          </div>
        </div>
      </slide>
    </carousel>
  </div>
</template>

<script>
import http from "@/utils/http-require.js";

import store from "@/store";
import { Carousel, Slide } from "vue-carousel";
export default {
  data() {
    return {
      store,
      auctions: []
    };
  },
  methods: {
    deletedata(auction, index) {
      http
        .delete(`auction/delete/${auction.auctionId}`)
        .then(alert(`${auction.title} 삭제됨`), this.auctions.splice(index, 1))
        .catch(console.log("Fail"));
    },
    getThumb(auction) {
      console.log(auction.thumbnail);
      console.log(auction.thumbnail.length);
      if (auction.thumbnail.length > 16) {
        return `http://192.168.100.83:9999/thumbnail/${auction.thumbnail}`;
      } else {
        // eslint-disable-next-line no-undef
        return require(`@/assets/img/frontPageLogo/6.png`);
      }
    }
  },
  mounted() {
    http
      .get(`auction/searchByMembers/${store.state.members.membersId}`)
      .then(res => {
        this.auctions = res.data;
      });
  },
  components: {
    Carousel,
    Slide
  }
};
</script>

<style>
#auction-list {
  list-style: none;
}
#AuctionCancel > table {
  border: 1px solid;
  border-collapse: separate;
  width: 100%;
  border-radius: 6px;
  border-spacing: 0;
  text-align: center;
}
td,
th {
  border: 1px solid;
}
table tr:first-child th:first-child {
  border-top-left-radius: 6px;
}

table tr:first-child th:last-child {
  border-top-right-radius: 6px;
}

table tr:last-child td:first-child {
  border-bottom-left-radius: 6px;
}

table tr:last-child td:last-child {
  border-bottom-right-radius: 6px;
}

thead {
  background-color: darkgray;
}
</style>