<template>
  <carousel :per-page="1" :mouse-drag="true" :autoplay="true" :loop="true">
    <slide>
      <div class="w3-row-padding">
        <div
          class="w3-third w3-margin-bottom"
          v-for="myAuction in myAuctionData"
          :key="myAuction.id"
        >
          <ul class="w3-ul w3-border w3-white w3-center w3-opacity w3-hover-opacity-off">
            <img src="https://placehold.it/320" style="width:100%" class="w3-hover-opacity" />
            <li class="w3-padding-16">{{myAuction.title}} [{{myAuction.tag}}]</li>
            <li class="w3-padding-16">{{myAuction.contents}}</li>
            <li class="w3-padding-16">
              <i class="fa fa-star font-size-h6 color-dark-yellow-400" />
              4.9
              <h4 class="rate w3-show-inline-block">651개의 평가</h4>
            </li>
            <li class="w3-padding-16">
              <h2>₩ {{myAuction.maxPrice}}</h2>
            </li>
            <li class="w3-light-grey w3-padding-24">
              <button class="w3-button w3-teal w3-padding-large w3-hover-black">조회하기</button>
            </li>
          </ul>
        </div>
      </div>
    </slide>
  </carousel>
</template>

<script>
// @ is an alias to /src
import { Carousel, Slide } from "vue-carousel";
import http from "@/utils/http-require.js";
import store from "@/store";

export default {
  name: "Auction",
  components: {
    Carousel,
    Slide
  },
  data: () => {
    return {
      //vuex part
      store,
      userInfoVo: {
        membersId: store.state.members.membersId
      },
      myAuctionData: {}
    };
  },
  mounted() {
    http
      // .get("/auction/searchByMembers/" + this.userInfoVo.membersId)
      .get("/auction/searchByMembers/nyapy")
      .then(res => {
        console.log(this.userInfoVo.membersId);
        this.myAuctionData = res.data;
      })
      .catch(res => {
        console.log(res);
      });
  }
};
</script>