import Vue from "vue";
import Router from "vue-router";
import FrontPage from "../views/FrontPage.vue";
import BackPage from "../views/BackPage.vue";
import Home from "../components/home/Home.vue";
import About from "../components/about/About.vue";
import CommandCenter from "../components/commandCenter/CommandCenter.vue";
import SignUp from "../components/front/SignUp.vue";
import SignUpSecond from "../components/front/SignUpSecond.vue";
import Notice from "../components/commandCenter/Notice.vue";
import NoticeInsert from "../components/commandCenter/NoticeInsert.vue";
import NoticeDetail from "../components/commandCenter/NoticeDetail.vue";
import NoticeUpdate from "../components/commandCenter/NoticeUpdate.vue";
import FeedbackDetail from "../components/commandCenter/FeedbackDetail.vue";
import FindPassword from "../components/front/FindPassword.vue";
import store from "@/store/index";

Vue.use(Router);

const requireAuth = () => (to, from, next) => {
  if (store.state.isAuth) {
    return next();
  }
  alert("Please login!!!");
  next("/");
};

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "FrontPage",
      component: FrontPage
    },
    {
      path: "/BackPage",
      name: "BackPage",
      component: BackPage,
      beforeEnter: requireAuth()
    },
    {
      path: "/Home",
      name: "Home",
      component: Home,
      beforeEnter: requireAuth()
    },
    {
      path: "/About",
      name: "About",
      component: About,
      beforeEnter: requireAuth()
    },
    {
      path: "/CommandCenter",
      name: "CommandCenter",
      component: CommandCenter,
      beforeEnter: requireAuth()
    },
    {
      path: "/SignUp",
      name: "SignUp",
      component: SignUp,
      props: true
    },
    {
      path: "/FindPassword",
      name: "FindPassword",
      component: FindPassword
    },
    {
      path: "/SignUpSecond",
      name: "SignUpSecond",
      component: SignUpSecond,
      props: true,
      beforeEnter: requireAuth()
    },
    {
      path: "/Notice",
      name: "Notice",
      component: Notice,
      beforeEnter: requireAuth()
    },
    {
      path: "/NoticeInsert",
      name: "NoticeInsert",
      component: NoticeInsert,
      beforeEnter: requireAuth()
    },
    {
      path: "/NoticeDetail",
      name: "NoticeDetail",
      component: NoticeDetail,
      props: true,
      beforeEnter: requireAuth()
    },
    {
      path: "/NoticeUpdate",
      name: "NoticeUpdate",
      component: NoticeUpdate,
      props: true,
      beforeEnter: requireAuth()
    },
    {
      path: "/FeedbackDetail",
      name: "FeedbackDetail",
      component: FeedbackDetail,
      props: true,
      beforeEnter: requireAuth()
    }
  ]
});
