<template>
  <div>
    <header>
      <a href="#">
        <img
          src="http://via.placeholder.com/600x300"
          style="width:65px;"
          class="w3-circle w3-right w3-margin w3-hide-large w3-hover-opacity"
        />
      </a>
      <span class="w3-button w3-hide-large w3-xxlarge w3-hover-text-grey" onclick="w3_open()">
        <i class="fa fa-bars"></i>
      </span>
      <div class="w3-container">
        <h1>
          <b>Some8ody</b>
        </h1>
        <div class="w3-section w3-bottombar w3-padding-16">
          <span class="w3-margin-right">Filter:</span>
          <button
            :class="{'w3-button w3-white': flag_all != 1, 'w3-button w3-black': flag_all == 1}"
            @click="changeFlag('All')"
            id="All"
          >ALL</button>
          <button
            class="w3-button w3-white"
            v-for="tagItem in tag"
            :key="tagItem.id"
            :id="tagItem"
            @click="changeFlag(tagItem)"
          >{{ tagItem }}</button>
        </div>
      </div>
    </header>
    <!-- First Photo Grid-->
    <div class="w3-row-padding">
      <div
        class="w3-third w3-container w3-margin-bottom"
        v-for="page in this.page_view"
        :key="page.id"
      >
        <img src="https://placehold.it/320x240" style="width:100%" class="w3-hover-opacity" />
        <div class="w3-container w3-white">
          <p>
            <b>{{auctionData[page].title}}</b>
          </p>
          <p>{{auctionData[page].contents}}</p>
        </div>
      </div>
    </div>

    <!-- Pagination -->
    <div class="w3-center w3-padding-32">
      <div class="w3-bar">
        <a href="#" class="w3-bar-item w3-button w3-hover-black">«</a>
        <a
          class="w3-bar-item w3-white w3-button"
          v-for="pageIdx in this.pageTotal"
          :key="pageIdx.id"
          @click="changePage"
        >{{pageIdx}}</a>
        <a href="#" class="w3-bar-item w3-button w3-hover-black">»</a>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/utils/http-require.js";
export default {
  name: "PhotoGrid",
  components: {},
  data: () => {
    return {
      //aution tag->binding
      tag: ["Design", "Photos", "Art", "Develop"],
      //all tag flag
      flag_all: 1,
      flag_list: [],

      keyword: "",
      page_list: [],
      page_view: 6,
      pageNum: 1,
      pageTotal: 1,
      auctionData: {}
    };
  },
  mounted() {
    http
      .get("auction/searchAll")
      .then(res => {
        this.auctionData = res.data;
        this.pageTotal = parseInt(res.data.length / 6 + 1);
        for (let i = 0; i < this.pageTotal; i++) {
          this.page_list.push(i);
        }
      })
      .catch(res => {
        console.log(res);
      });
  },
  methods: {
    changeFlag(item) {
      if (!this.flag_list.includes(item) && item != "All") {
        this.flag_list.push(item);
        document.getElementById(item).classList.add("w3-black");
        this.flag_all = -1;
        this.keyword = this.flag_list.join("|");
      } else if (this.flag_list.includes(item) && item != "All") {
        let index = this.flag_list.indexOf(item);
        this.flag_list.splice(index, 1);
        document.getElementById(item).classList.remove("w3-black");
        this.flag_all = -1;
        this.keyword = this.flag_list.join("|");
      } else if (item == "All") {
        for (let idx in this.flag_list) {
          let data = this.flag_list[idx];
          document.getElementById(data).classList.remove("w3-black");
        }
        this.flag_list = [];
        this.flag_all *= -1;
        this.keyword = "All";
      }

      if (this.flag_list.length == 0) {
        this.keyword = "All";
        this.flag_all = 1;
      }

      console.log(this.flag_list);
      console.log(this.keyword);

      http
        //http://192.168.100.83:9999/auction/searchByTag/
        .get("auction/searchByTag/" + this.keyword)
        .then(res => {
          console.log(res);
          this.auctionData = res.data;
          this.pageTotal = parseInt(res.data.length / 6 + 1);

          // console.log(this.auctionData);
        })
        .catch(res => {
          console.log(res);
        });
    },
    changePage() {
      console.log(this.page_list);
    }
  }
};
</script>

