<template>
  <div id="AuctionRegist" class="w3-container w3-card-4 w3-light-grey">
    <h2>경매 등록</h2>
    <form
      action="#"
      id="auctionRegist"
      method="post"
      @submit.prevent="auctionRegist"
      enctype="multipart/form-data"
    >
      <input type="hidden" name="membersId" :value="auction.membersId" />
      <div>
        <div>
          <label>Thumbnail</label>
          <input
            type="file"
            @change="previewImage"
            accept="image/*"
            name="thumbnailFile"
            class="w3-input w3-border"
          />
        </div>
        <div class="image-preview" v-if="imageData.length > 0">
          <img class="preview" :src="imageData" />
        </div>
      </div>
      <div class="w3-section">
        <label>Deadline</label>
        <input
          name="deadline"
          class="w3-input w3-border"
          type="datetime-local"
          v-model="auction.deadline"
          placeholder="Deadline"
        />
      </div>

      <div class="w3-section">
        <label>Title</label>
        <input
          v-model="auction.title"
          class="w3-input w3-border"
          type="text"
          name="title"
          placeholder="title"
        />
      </div>
      <div class="w3-section">
        <label>Price</label>
        <input
          v-model="auction.maxPrice"
          class="w3-input w3-border"
          type="text"
          value="10000"
          name="maxPrice"
        />
      </div>

      <div class="w3-section">
        <label>Contents</label>
        <br />
        <textarea
          style="resize:none"
          v-model="auction.contents"
          class="w3-input w3-border"
          id="contents"
          name="contents"
          rows="10"
          placeholder="내용"
        ></textarea>
      </div>

      <div class="w3-section">
        <label>Online/Offline</label>
        <br />
        <input class type="radio" v-model="auction.online" name="online" value="1" /> Online
        <input class type="radio" v-model="auction.online" name="online" value="0" /> Offline
      </div>

      <div class="w3-section">
        <label>Details</label>
        <input class="w3-input w3-border" type="file" name="detailFile" />
      </div>

      <div class="w3-section">
        <label>Auction Period</label>
        <input
          v-model="auction.salesTime"
          name="salesTime"
          id="sales_time"
          class="w3-input"
          type="datetime-local"
          style="width:40%"
          required
        />
      </div>
      <div class="w3-section">
        <label>Tag</label>
        <input v-model="auction.tag" class="w3-input w3-border" type="text" name="tag" />
      </div>

      <button type="submit" class="w3-button w3-black w3-margin-bottom">
        <i class="fa fa-paper-plane w3-margin-right"></i> Register
      </button>
    </form>
    <hr style="height:2px; background-color:lightgray" />
  </div>
</template>

<script>
import http from "@/utils/http-require.js";

import store from "@/store";

export default {
  data() {
    return {
      imageData: "",
      auction: {
        auctionId: "",
        contents: "",
        deadline: "",
        file: "",
        membersId: store.state.members.membersId,
        name: "",
        online: "",
        salesTime: "",
        tag: "",
        title: "",
        maxPrice: ""
      }
    };
  },
  created() {},
  methods: {
    auctionRegist() {
      let formData = new FormData(document.getElementById("auctionRegist"));
      console.log(this.auction);
      var dead1 = this.auction.deadline.slice(0, 10);
      var dead2 = this.auction.deadline.slice(11, 16);
      var sale1 = this.auction.salesTime.slice(0, 10);
      var sale2 = this.auction.salesTime.slice(11, 16);
      this.auction.salesTime = sale1 + " " + sale2;
      this.auction.deadline = dead1 + " " + dead2;
      console.log(this.auction.salesTime);
      console.log(this.auction.deadline);
      formData.set("salesTime", this.auction.salesTime);
      formData.set("deadline", this.auction.deadline);

      http
        .post("auction/insert", formData)
        .then(res => {
          console.log(formData);
          alert("글 작성됨");
          console.log(res.data.state);
          this.$store.commit("pageOf", {
            mainPage: "home",
            subPage: "Header"
          });
        })
        .catch(console.log(formData));
    },
    previewImage: function(event) {
      var input = event.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(input.files[0]);
      }
    }
  }
};
</script>

<style>
</style>