<template>
  <div id="ProfileModify">
    <form
      class="w3-container w3-card-4 w3-light-grey"
      action="#"
      id="userEdit"
      method="post"
      @submit.prevent="userEdit"
      enctype="multipart/form-data"
    >
      <h2>내 정보 수정</h2>
      <div class="w3-section">
        <label>Name</label>
        <input class="w3-input w3-border" type="text" v-model="member.name" />
      </div>
      <div>
        <div>
          <label>Profile Image</label>
          <input
            type="file"
            @change="previewImage"
            accept="image/*"
            name="imageFile"
            class="w3-input w3-border"
          />
        </div>
        <div class="image-preview" v-if="imageData.length > 0">
          <img class="preview" :src="imageData" />
        </div>
      </div>

      <div class="w3-section">
        <label>Email</label>
        <input class="w3-input w3-border" type="text" :value="member.email" disabled />
      </div>

      <div class="w3-section">
        <label>Password</label>
        <input
          v-model="member.password"
          class="w3-input w3-border"
          type="password"
          placeholder="password"
          required
        />
      </div>
      <div class="w3-section">
        <label>Portfolio</label>
        <input class="w3-input w3-border" type="file" name="portfolioFile" />
      </div>

      <div class="w3-section">
        <label>Address</label>
        <input class="w3-input w3-border" type="text" name="address" v-model="member.address" />
      </div>
      <div class="w3-section">
        <label>Phone number</label>
        <input class="w3-input w3-border" type="text" name="phone" v-model="member.phone" />
      </div>
      <div class="w3-section">
        <label>Contact time</label>
        <input id="Contacttime" class="w3-input" type="time" name="Open" value="09:00" required />
        <input id="Contacttime" class="w3-input" type="time" name="Close" value="18:00" required />
      </div>
      <button type="submit" class="w3-button w3-black w3-margin-bottom">
        <i class="fa fa-paper-plane w3-margin-right"></i> Modify
      </button>
    </form>

    <hr style="height:2px; background-color:lightgray" />
  </div>
</template>

<script>
import http from "@/utils/http-require.js";
// import http from "@/utils/http-require.js";
import store from "@/store";
export default {
  data() {
    return {
      store,
      imageData: "",
      userId: store.state.members.membersId,
      member: {
        membersId: store.state.members.membersId,
        name: store.state.members.name,
        email: store.state.members.email,
        account: store.state.members.account,
        address: store.state.members.address,
        phone: store.state.members.phone,
        image: store.state.members.image,
        portfolio: store.state.members.portfolio,
        password: "",
        imageFile: null,
        portfolioFile: null,
        rank: 0
      }
    };
  },
  methods: {
    previewImage: function(event) {
      var input = event.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    userEdit() {
      console.log(this.member);
      http
        .put(`member/update`, this.member)
        .then(() => {
          alert(`수정되었습니다.`);
          this.$router.push("/#");
        })
        .catch(() => {
          alert("수정실패");
        });
    }
  }
  // created() {
  //   http
  //     .get(`member/search/${this.userId}`)
  //     .then(res => {
  //       console.log(res);
  //       this.member.membersId = res.data.membersId;
  //       this.member.name = res.data.name;
  //       this.member.email = res.data.email;
  //       this.member.address = res.data.address;
  //       this.member.phone = res.data.phone;
  //       this.member.image = res.data.image;
  //       this.member.portfolio = res.data.portfolio;
  //     })
  //     .catch(console.log("였먹어2"));
  // }
};
</script>

<style>
#Contacttime {
  width: 45%;
}
</style>